import React, { useState, useEffect } from 'react';
// route.
import { useParams } from "react-router-dom";
// aave action buttons.
import BalanceIsStatus from '../../base/failures/BalanceIsStatus'
//
import AaveAction from '../poolData-helpers/AaveAction';
import AaveRouteActions from '../poolData-helpers/AaveRouteActions';
import Icon from '../../base/icon/Icon';
import Loader from '~BASE/loader/loader'
import PlzConnectWalletMan from '../../base/failures/PlzConnectWalletMan'

import { abi as IErc20 } from '~ABIs/abis/erc20.json';
import UsdPrice from '../../base/UsdPrice/UsdPrice';
// .
let Web3 = require('web3');
import { ethers } from "ethers";
// React context.
import { useStaticPoolDataContext } from '~GRAPHQL/static_graph-pool-data-provider_formated';
import { useContentContextState } from '~/Code/Components/content_context';

import Transaction_status from '~BASE/Transaction_status/Transaction_status'

//
const DepositConfirm = ({ 
	selectedProvider, injectedProvider,
	loadWeb3Modal, web3Modal, 
	assetDataOneReserves
}) => {

// Context
	const { 
		contracts, setUserAccountData,
		actionTransactionDataState,
		setPoolDataState
	} = useContentContextState();
	const { assetData } = setPoolDataState
	const { userAccountData } = setUserAccountData 
	const { transactionReceipt, transactionHash } = actionTransactionDataState
//
    const { lendingPoolContract } = contracts
// Context setting end.
	const { setIsMobileWidthVersion, LENDING_POOL_ADDRESS } = useStaticPoolDataContext()
	const { isMobile } = setIsMobileWidthVersion

	

// ~~~

// Use route param.
    let { symbol, amount, underluing } = useParams();
	const amoutFormatted = amount.replace(/[^0-9.,-]/g, '').toString();

// State.
	const [balance, setBalance] = useState();
// Get user addres. erc20 balance.
	const getTokenBalance = async () => {
        let tokenContract = new ethers.Contract(underluing, IErc20, signer);
        let address = await signer.getAddress()
        let _balance = await tokenContract.balanceOf(address)
        setBalance(+assetDataOneReserves?.decimals === 8 
			? _balance.toString()
			: _balance.toString()
		)
    }
	useEffect(() => {
        if(underluing && assetData.length === setPoolDataState.assetsActiveLength) {
            getTokenBalance();
        }
    }, [assetDataOneReserves])
	// console.log('amoutFormatted', transactionHash)
// 
    let signer = selectedProvider?.getSigner();

// Check if need approve.
	const [allowanceState, setAllowance] = useState();
	const getAllowanceStatus = async () => {
		let tokenContract = new ethers.Contract(underluing, IErc20, signer);
		let address = await signer.getAddress()
		let _allowance = await tokenContract.allowance(address, LENDING_POOL_ADDRESS)
		setAllowance(_allowance)
	}
	useEffect(() => {
		getAllowanceStatus()
	}, [] )
	console.log('allowallowanceStateanceState', allowanceState)

//
	const [amountWeiResult, setAmountWeiResult] = useState(0);
	const amountToWei = () => {
		let amountMinusString = amount.substring(7)
		console.log('amountMinusString', amountMinusString)
		setAmountWeiResult(amountMinusString)
	}
	useEffect(() => {
		amountToWei();
	}, [amount, assetDataOneReserves] )

//
	const [isLoad, setLoaded] = useState(false)
    useEffect(() => {
        setLoaded(true)
    }, [])


	useEffect(() => {
		window.scroll(0, 250);
	} ,[]) 

//
    return (
		 assetDataOneReserves && injectedProvider
			? 
        <div className={`${ isLoad ? 'active_block' : 'hidden_block' }`}>
			<div className="col card mb-30">
				{/* @@include("../blocks/modules/card-top/card-top-deposit.html", { */}
				{/* <!-- Expand or Minimize --> */}
			</div>
				<div className={`${transactionReceipt?.status === 1 ? 'borderGreen' : ''} col card grow-1`}>


					<AaveRouteActions type={'back'} />

					<div className={`${ isLoad ? 'ahashakeheartache' : '' } card__content--md card__content card__content--md p-60-24 m-auto`}>

						<Icon
							showImg={true}
							showSpan={false}
							symbolChild={symbol}
							directory={'/assets-main'}
						/>

						<Transaction_status 
							action={'Deposit'}
						/>

						<div className="detail__body mt-40">

							<div className={` ${isMobile ? 'mobile-detail-body' : 'grid-row grid-row--mb-20 mt-50' } `} >

								{!isMobile &&
									<div style={{textAlign: 'left'}} className={` ${!isMobile ? 'label label--md' : 'label label--md flex-col' } `}>
										{amount ? 'Amount' :'Available to deposit'}
									</div>
								}
								
								<div className="grid-row__right">
									
									{isMobile &&
										<div  className={` ${!isMobile ? 'label label--md' : 'label label--md ' } `}>
											{amount ? 'Amount' :'Available to deposit'}
										</div>
									}
									
									<div className='value'>
										{ amountWeiResult && amoutFormatted }
									</div>


								</div>
								<div className="grid-row__right">
									<div className="label label--md">
										{ amountWeiResult &&
											<UsdPrice
												symbol={assetDataOneReserves?.symbol}
												tableBool={true}
												decimals={+assetDataOneReserves?.decimals}
												offUsdSymbols={false}
												valueCalculate={assetDataOneReserves?.priceOracle} 
												selectedProvider={selectedProvider}
												currentBalanceForUserTokenChildInWei={amountWeiResult} 
											/>
										}
									</div>
								</div>

							</div>

							<div className="grid-row grid-row--mb-40">
								<div className="grid-row__base">
									<div className="progress">
										<ul className={`progress__steps 
											${ transactionReceipt?.status === 1
													? 'step-3'
													: transactionHash?._transactionHash
														? 'step-2'
														: 'step-1'
											}`}>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
												<li></li>
										</ul>

										<ul className="progress__labels">
											{allowanceState && allowanceState.toString() !== '0' 
												? '' 
												: <li className="label--md label--dark">Approve</li>
											}
												<li className="label--md label--dark">Deposit</li>
												<li className="label--md label--dark">Finished</li>
										</ul>

									</div>
								</div>
							</div>


								{/* <div className="grid-row__left">
									<div className="value mb-5">Approve</div>
									<p className="label label--wrap">Please approve before depositing.</p>
								</div> */}

							
							<div className='grid-row'>
								
								<div className='grid-row__right a-end'>
									{transactionHash?._transactionHash
										? <Loader />
										: transactionReceipt?.status !== 1 &&
											<AaveAction
												type={allowanceState && allowanceState.toString() === '0' ? 'approve' : 'deposit' } 
												buttonClassNameChild="btn btn--red btn--small"
												divWrapperClassNameChild='text-center'
												signer={signer} 
												amountFromRouteUrl={amount.substring(7).toString()} 
												lendingPoolContract={lendingPoolContract} 
												userAccountData={userAccountData}  
												selectedProvider={selectedProvider}
												assetData={assetDataOneReserves}
											/>
									}
								</div>
							</div>

						</div>

					</div>

				</div>
			</div>

			: assetDataOneReserves === undefined
				? <BalanceIsStatus
					loadWeb3Modal={loadWeb3Modal} 
					action={'deposit'}
					selectedProvider={selectedProvider} 
					web3Modal={web3Modal} 
					walletBalanceInHumanityDecimals={balance}
				/>		
				: <PlzConnectWalletMan 
					loadWeb3Modal={loadWeb3Modal} 
					web3Modal={web3Modal} 
				/> 					
		
    )
}



export default React.memo(DepositConfirm)




