import React, { useEffect, useMemo, useState, useCallback } from 'react'
// 
import { useStaticPoolDataContext } from '~GRAPHQL/static_graph-pool-data-provider_formated'
// import { useContentContextState } from '~/Code/Components/content_context'

import { useTable, useSortBy } from "react-table"

// import { useAaveData } from "../poolData-helpers/AaveData"
import AaveRouteActions from "../poolData-helpers/AaveRouteActions"
import Icon from '../../base/icon/Icon'
import UsdPrice from '../../base/UsdPrice/UsdPrice'
import Loader from '~BASE/loader/loader'

// WEb3.
import { ethers } from "ethers"
import { abi as IErc20 } from '~ABIs/abis/erc20.json'
let Web3 = require('web3')
//
const DepositMarket = ({ 
    selectedProvider, 
    injectedProvider, 
    web3Modal, 
    assetData,
    reserveTokens 
}) => {
//
	const {
        setIsMobileWidthVersion,
    } = useStaticPoolDataContext();
    const { isMobile } = setIsMobileWidthVersion
//
    let [balance, setBalance] = useState([]);
// Get user addres.
    let signer = injectedProvider?.getSigner();
    console.log('balancdaesignerbalance', signer)

// erc20 balance.
    const getTokenBalance = async () => {
        for (const addressIterator of reserveTokens){
            // console.log('addressIterator', addressIterator)
            let tokenContract = new ethers.Contract(addressIterator.tokenAddress, IErc20, signer);
            let address = await signer.getAddress()
            console.log('reservreserveTokenseTokens', address)
            let _balance = await tokenContract.balanceOf(address)
            setBalance((prevValue) => [...prevValue, { symbol: addressIterator.symbol, balancee: _balance.toString() } ])
        }
    }
    //
    useEffect(() => {
        if(web3Modal.cachedProvider.toString().length === '0'){
            setBalance([])
        }
    }, [])

    
//
    useEffect(() => {
        if(reserveTokens && web3Modal.cachedProvider.toString().length !== '0') {
            getTokenBalance()
        }
    }, [reserveTokens])

    const columns2 = useMemo(
        () => [
    
          {
            Header: ' ',
            accessor: value => <div style={{position: 'absolute', right: '55%', top: '50%'}}> <Loader /> </div>,
          },
          
        ]
      )
//
    const columns = useMemo(
        () => [
            {
                Header: 'Token',
                accessor: value =>
                    <Icon
                        showImg={true} showSpan={true} 
                        symbolChild={value.symbol} 
                        directory={'/assets-main'} 
                    />
                ,
            },
            {
                Header: 'You wallet balance',
                accessor: value => {
                    if(balance.length > 0){
                        for (const key in balance) {
                            const token = balance[key];
                              if(token.symbol === value.symbol && +value.decimals === 18){
                                return (
                                <div className='walletBal'>
                                    {isMobile ? <span>You wallet balance</span> : ''}
                                   
                                    {/* <p className='table-value table-value--dark mb-8'>{token.balancee !== undefined ? Web3.utils.fromWei(token.balancee.toString(), 'ether').toString().substring(0, +value.decimals) : 'das' }</p> */}
                                    <span>
                                        <UsdPrice
                                            symbol={value.symbol}
                                            tableBool={true}
                                            decimals={+value.decimals}
                                            currentBalanceForUserTokenChildInWei={Web3.utils.fromWei(token?.balancee, 'ether')}
                                            offUsdSymbols={false}
                                            valueCalculate={value.priceOracle.toString()}
                                            selectedProvider={selectedProvider}
                                        />
                                    </span>
                                </div> )
                            } if(token.symbol === value.symbol && +value.decimals === 6){
                                return (
                                    <div className='walletBal'>
                                     {isMobile ? <span>You wallet balance</span> : ''}
                                    {/* <p className='table-value table-value--dark mb-8'>{ Web3.utils.fromWei(token.balancee.toString(), 'mwei').toString().substring(0, +value.decimals) }</p> */}
                                    <span>
                                        <UsdPrice 
                                            symbol={value.symbol}
                                            tableBool={true} decimals={value.decimals} currentBalanceForUserTokenChildInWei={Web3.utils.fromWei(token.balancee, 'mwei')}
                                            offUsdSymbols={false} valueCalculate={value.priceOracle.toString()} selectedProvider={selectedProvider}
                                        />
                                    </span>
                                </div> )
                            } if(token.symbol === value.symbol && +value.decimals === 8){
                                return (
                                    <div className='walletBal'>
                                        {isMobile ? <span>You wallet balance</span> : ''}
                                      {/* <p className='table-value table-value--dark mb-8'>{  Web3.utils.fromWei((token.balancee.toString() + '0'), 'gwei').toString().substring(0, +value.decimals) }</p> */}
                                      <span>
                                        <UsdPrice 
                                            symbol={value.symbol}
                                            tableBool={true} decimals={value.decimals} currentBalanceForUserTokenChildInWei={Web3.utils.fromWei(token.balancee, 'gwei') }
                                            offUsdSymbols={false} valueCalculate={value.priceOracle} selectedProvider={selectedProvider}
                                        />
                                      </span>
                                  </div> )
                            }
                        }
                    } else {
                        return (
                            isMobile 
                                ? <div className='walletBal'>
                                    <span>You wallet balance</span>
                                    <span>-</span> 
                                    </div>
                                : '-'
                        )
                    }

                } 
            },
            {
                Header: 'Deposit APR',
                accessor: value => {
                    
                    //
                    let RAY = 10 ** 27
                    let resultForAprSumm = value.liquidityRate
                    let depositAPR = Number((+resultForAprSumm / RAY)) === 0 
                        ? '0 %'
                        : Number((+resultForAprSumm / RAY) * 100).toFixed(4) + ' %'

                    
                    value.symbol === 'WBTC' && console.log('resultForAprSumm', +value.liquidityRate)
                    //
                    return isMobile 
                        ? <div 
                            className='value'
                            style={{ width: '100%', justifyContent: 'space-between', display: 'flex' }}>
                                <span>Deposit APR</span> 
                                <span>{depositAPR}</span> 
                          </div>
                        : depositAPR
                    // return parseFloat(formatUnits(value.liquidityRate, 25)).toString().substring(0, 4) + ' %'
                }
            },
            // {
            //     Header: 'Stable APR',
            //     accessor: value => {
            //         let RAY = 10**27 // 10 to the power 27
            //         let stableBorrowAPR = Number(value.stableBorrowRate) / RAY
          
            //         return stableBorrowAPR.toString().substring(0, 4) + ' %'
            //     }
            // },
            {
                Header: 'Actions',
                accessor: value => {
                    return ( 
                        <>
                            <div className='walletBal'>
                                {isMobile ? 'Actions' : ''}
                                <AaveRouteActions 
                                    type={'deposit'} 
                                    classNameChild={"btn btn--red btn--small"} 
                                    assetData={value} 
                                />
                            </div>
                        </> 
                    )
                }
            },
        ]
    )

const Table = useCallback(({ columns, balanceee, data, filterable }) => {
// Use the state and functions returned from useTable to build your UI
    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        } = useTable({ columns, data, balanceee },
            useSortBy       
        )

    const [isLoad, setLoaded] = useState(false)

    useEffect(() => {
        setLoaded(true)
    }, [])

    return (
        <div className={`${ isLoad ? 'active_block' : 'hidden_block' }`}>

        
        <div className='table__content table__content--classic table-offset'>


        <div className='scroll-content'>
            <table className='table' {...getTableProps()} >
                { !isMobile ? 
                    <thead>
                        {headerGroups.map(headerGroup => (
                            <tr className='table-header__body' {...headerGroup.getHeaderGroupProps()}>
                                {headerGroup.headers.map(column => (
                                <th className='table-header__col' 
                                    {...column.getHeaderProps(column.getSortByToggleProps())}
                                >
                                    {column.render('Header')}
                                    <span>
                                        {column.isSorted
                                            ? column.isSortedDesc
                                                ? ' 🔽'
                                                : ' 🔼'
                                            : ''
                                        }
                                    </span>
                                </th>
                                ))}
                            </tr>
                        ))}
                    </thead>

                    : ''
                }
                <tbody {...getTableBodyProps() }>
                    {rows.map(row => {
                        prepareRow(row)
                        return (
                            <tr 
                                style={{ width: '100%' }} 
                                className='table-item table-item table-value table-value--dark mb-8' {...row.getRowProps() }>
                                {row.cells.map(cell => {
                                    return (
                                        <td className='dataTables'
                                            {...cell.getCellProps()}
                                        >
                                            {cell.render('Cell')}
                                        </td>
                                    )
                                })}
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        </div>
    </div>


        </div>
    )
    }, []
)

// !!

    return (
        <>
            <Table
                data={
                    assetData?.length === 0 && reserveTokens
                        ? reserveTokens 
                        : assetData
                }
                columns={assetData.length === 0 ? columns2 : columns} 
            />
        </>
    )
}




export default React.memo(DepositMarket)
