import React, { useState, useEffect, useContext } from 'react';
import queryString from 'query-string';
// Router.
import { useHistory, useParams, Link } from "react-router-dom";
// .
// import AaveAction from '../poolData-helpers/AaveAction'
import AaveRouteActions from '../poolData-helpers/AaveRouteActions'
// import { useAaveData } from "../poolData-helpers/AaveData"
import { useStaticPoolDataContext } from '~GRAPHQL/static_graph-pool-data-provider_formated';
import { useContentContextState } from '~/Code/Components/content_context';


import Icon from '../../base/icon/Icon';


import PlzConnectWalletMan from '../../base/failures/PlzConnectWalletMan'
import BalanceIsStatus from '../../base/failures/BalanceIsStatus'
import UsdPrice from '../../base/UsdPrice/UsdPrice';
// web3.
import { ethers } from "ethers";
let Web3 = require('web3');
import { abi as IErc20 } from '~ABIs/abis/erc20.json';
//
const DepositMaker = ({ 
	injectedProvider, selectedProvider, 
	web3Modal, loadWeb3Modal,
	assetDataOneReserves
 }) => {
	const { setUserAccountData } = useContentContextState();
	//
	const { setIsMobileWidthVersion } = useStaticPoolDataContext()
	const { isMobile } = setIsMobileWidthVersion

// Route params.
	let { symbol, underluing } = useParams();
	let signer = selectedProvider?.getSigner();

	
	const [isLoad, setIsLoad] = useState(false)

	useEffect(() => {
		setIsLoad(true)
	}, [])


	useEffect(() => {
		window.scroll(0, 250);
	} ,[]) 

	
//
	// Get hash address from react ro uter.
// State.
	const [balance, setBalance] = useState();
	console.log('dasdadsadasdasd', balance)
// Get user addres. erc20 balance.
	const getTokenBalance = async () => {
        let tokenContract = new ethers.Contract(underluing, IErc20, signer);
        let address = await signer.getAddress()
        let _balance = await tokenContract.balanceOf(address)
        setBalance(+assetDataOneReserves?.decimals === 8 
			? _balance.toString()
			: _balance.toString())
    }
	useEffect(() => {
        if(underluing && web3Modal?.cachedProvider) {
            getTokenBalance();
        }
    }, [underluing && web3Modal?.cachedProvider])
//
	const [walletBalanceInHumanityDecimals, setWalletBalanceInHumanityDecimals] = useState();
	const [amountDepositAll, setUserAmountDepositAll] = useState(false)
	const [amountDeposit, setUserAmoutDeposit] = useInput({ className: 'amount__input', type: 'text', walletBalanceInHumanityDecimals })
// Using with useEffect strings 
	console.log('c', balance)
	console.log('walletBalanceInHubalaassetDataOneReservesncemanityDecimals', assetDataOneReserves)


//
	useEffect(() => {
		if(assetDataOneReserves && balance){
			  if(+assetDataOneReserves.decimals === 18) {
				setWalletBalanceInHumanityDecimals(Web3.utils.fromWei(balance, 'ether'));
			} else if(+assetDataOneReserves.decimals === 8 ) {
				setWalletBalanceInHumanityDecimals(Web3.utils.fromWei((balance), 'gwei'));
			} else if(+assetDataOneReserves.decimals === 6) {
				setWalletBalanceInHumanityDecimals(Web3.utils.fromWei(balance, 'mwei'));
			}
		}
	}, [assetDataOneReserves && balance])
//
	const [inputErorr, setInputError] = useState()
    useEffect(() => {
        if(inputErorr === true){
            const timer = setTimeout(() => {
                setInputError(false);
            }, 400);
                return () => clearTimeout(timer);
        }   
    }, [inputErorr])
//
	console.log('amountDepositAll', amountDepositAll);
//
	function useInput({ className, type }) {
		const [value, setValue] = useState();
		const input = <>
			<input 
				className={className} 
				value={value} 
				maxLength={+assetDataOneReserves?.decimals} 
				onChange={e => +e.target.value > walletBalanceInHumanityDecimals 
					? setValue(walletBalanceInHumanityDecimals) 
					: setValue(e.target.value) 
				} 
				type={type} 
			/>
			<button className='amount__btn' onClick={() => { setValue(walletBalanceInHumanityDecimals), setUserAmountDepositAll(true)} } type="depositAll">Max</button>
					  </>
		return [value, input];
	}

//
// let maxAmountToDeposit = new BigNumber(walletBalanceInHumanityDecimals);
//   if (walletBalanceInHumanityDecimals && maxAmountToDeposit.gt(0)) {
//     // keep it for tx gas cost
//     maxAmountToDeposit = maxAmountToDeposit.minus('0.001');
//   }
//   if (walletBalanceInHumanityDecimals && maxAmountToDeposit.lte(0)) {
//     maxAmountToDeposit = valueToBigNumber('0');
//   }

  console.log('maxAmountToDeposdsadt', injectedProvider)
  console.log('walletBalanceInHumanityDecimalsdsadas', walletBalanceInHumanityDecimals)
//
	const history = useHistory();	
	const handleSubmit = (amount, amountDepositAll) => {
		const query = queryString.stringify({
			amount: amountDepositAll ? amount : amount,
		});
		history.push(`${history.location.pathname}/${query}`);
	};
//
	let ethToWeiProp = +assetDataOneReserves?.decimals === 18 ? 'ether'
		: +assetDataOneReserves?.decimals === 6
			? 'mwei'
			: 'gwei'
//
    return (
        <>

			<div className={`${ isLoad ? 'active_block' : 'hidden_block' }`}>
				
				{
					assetDataOneReserves 
					&& web3Modal?.cachedProvider 
					&& walletBalanceInHumanityDecimals !== '0' 
					&& setUserAccountData.userAccountData

					?

						<>

					<div className="col card grow-1">

						{/* @@include("../blocks/modules/btn-back-card/btn-back.html") */}
						<AaveRouteActions type={'back'} classNameChild={"btn btn--red btn--small"}  />


						<div className={`${ isLoad ? 'ahashakeheartache' : '' } card__content card__content--md p-60-24 m-auto`}>

							<Icon
								showImg={true}
								showSpan={false}
								symbolChild={symbol}
								directory={'/assets-main'}
							/>

							<div className="detail__top mb-40 text-center">
								<div className='df-center mb-20'>
									<h2 className="title">How much would you like to deposit?</h2>
									<div className='ml-10'>
										
                                	</div>
								</div>
								<p className="description">
									Please enter an amount you would like to deposit. The maximum amount you can deposit is shown below.
								</p>
							</div>

							<div className="detail__body">

								<div className='grid-row grid-row--mb-20 mt-50'>

									{!isMobile &&
										<div className={` ${isMobile ? 'label label--md' : 'label label--md flex-col' } `}>
											Available to deposit
										</div>
									}

									<div className="grid-row__right">
										<div className='flex-col'>
											{isMobile &&
												<div className={` ${isMobile ? 'label label--md' : 'label label--md flex-col' } `}>
													Available to deposit
												</div>
 											}
											<div className="label">
												<div className="value">
													{ walletBalanceInHumanityDecimals && walletBalanceInHumanityDecimals + ' ' + symbol}
												</div>
											</div>
											<div className="label">
												<UsdPrice
													symbol={assetDataOneReserves?.symbol}
													tableBool={true} 
													decimals={+assetDataOneReserves?.decimals}
													offUsdSymbols={false} 
													valueCalculate={assetDataOneReserves?.priceOracle.toString()}
													selectedProvider={selectedProvider}
													currentBalanceForUserTokenChildInWei={walletBalanceInHumanityDecimals && walletBalanceInHumanityDecimals} 
												/> 
											</div>
										</div>
									</div>
								</div>

								<div className="flex a-center">
									<div className="grid-row__base">

										<div className={`amount mb-40 ${inputErorr ? `ahashakeheartache` : ''}`}>
											{setUserAmoutDeposit}
										</div>
										
									</div>
								</div>

									<div className="flex a-center mt-20">
										{/* @@include("../blocks/modules/loader/loader.html", 
										display: "none" */}
										{ 
										<button className='btn btn--red btn--big m-auto ' 
											onClick={ () => { 
												typeof(amountDeposit) !== 'undefined' 
													? handleSubmit(amountDeposit, amountDepositAll) 
													: setInputError(true)
												} }>
											Continue
										</button>
										}
									</div>

							</div>
						</div>
					</div>

			{/* ... */}

			</> : !injectedProvider 
					? <PlzConnectWalletMan loadWeb3Modal={loadWeb3Modal} web3Modal={web3Modal} /> 
					: <BalanceIsStatus 
						selectedProvider={selectedProvider} 
						web3Modal={web3Modal} 
						walletBalanceInHumanityDecimals={walletBalanceInHumanityDecimals === '0' ? 0 : undefined} 
						symbol={symbol} 
						action={'deposit'}
						loadWeb3Modal={loadWeb3Modal} />
				}

			</div>
    	</>
    )
}



export default React.memo(DepositMaker)





