import React, { useContext, useEffect, useState, useMemo, useRef } from 'react';
// Router.
import { useLocation, useParams, Switch, Route } from "react-router-dom";
// Components.
import { useContentContextState } from '~/Code/Components/content_context';

import { useStaticPoolDataContext } from '~GRAPHQL/static_graph-pool-data-provider_formated';
//
import DepositMarket from './DepositMarket';
import DepositMaker from './DepositMaker';
import DepositConfirm from './DepositConfirm';
import DepositHeader from './DepositHeader';
//
const Deposit = ({ injectedProvider, selectedProvider, web3Modal, loadWeb3Modal }) => {
//
	const { symbol } = useParams()
	const { setOnePoolDataState, setPoolDataState, setReserveTokensContext } = useContentContextState()
	const { assetData } = setPoolDataState

   // run this function from an event handler or an effect to execute scroll 
	//
	useEffect(() => {
		if(assetData?.length === setPoolDataState.assetsActiveLength && setOnePoolDataState.assetDataOneReserves?.symbol !== symbol){
			setOnePoolDataState.setSymbol(symbol)
			console.log('walletBalanceInHumanityDecimals')
		}
	}, [assetData] )
//
	let location = useLocation()

	
	
//
	return (
		<>
			<div className='container'>
			<div className={``}>
				<div className={`dashboard active`} >
					<div className="container">
						<div className="dashboard__body row-flex flex-col">


			{ symbol && <div className="col card mb-30">
				<DepositHeader selectedProvider={selectedProvider} />
			</div>}



		<Switch location={location}>
		
			<Route exact path='/deposit/:symbol/:underluing'
				component={() => (

						<DepositMaker
							assetDataOneReserves={setOnePoolDataState.assetDataOneReserves}
							
							web3Modal={web3Modal}
							loadWeb3Modal={loadWeb3Modal}
							selectedProvider={selectedProvider}
							injectedProvider={injectedProvider}
						/> 
				)}
			/>
			
			<Route path='/deposit/:symbol/:underluing/:amount?' 
				component={() => ( 
					<DepositConfirm 
						injectedProvider={injectedProvider}
						loadWeb3Modal={loadWeb3Modal} 
						selectedProvider={selectedProvider} 
						web3Modal={web3Modal}

						assetData={assetData}
						reserveTokens={setReserveTokensContext.reserveTokens}
						assetDataOneReserves={setOnePoolDataState.assetDataOneReserves}
					/> 
				)} 
			/>

		</Switch>

		<DepositMarket 
			selectedProvider={selectedProvider}  
			injectedProvider={injectedProvider}
			web3Modal={web3Modal} 

			assetData={assetData}
        	reserveTokens={setReserveTokensContext.reserveTokens}
		/>


						</div>
					</div>
				</div>
			</div>
			
			</div>
		</>
	)

}



export default React.memo(Deposit)

			// <!-- Header-bottom -->
			// @@include("../balanceNumToStringocks/modules/header-bottom/header-bottom-deposit.html")
	
		
